import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { LoaderService } from './loader.service';
@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  constructor(private http: HttpClient, private router: Router, private loaderService:LoaderService ) { }


  submitModel(text) {
    console.log(text)
     this.loaderService.show();
    return this.http.post<any>(`http://localhost:5000/get-suggestion`, {'text':text})
        .pipe(map(response => {
             this.loaderService.hide();
            return response;
        }));

}

submitText(files) {
  console.log(files)
  // this.loaderService.show();
  return this.http.post<any>(`http://localhost:5000/correct_doc_file`, files)
      .pipe(map(response => {
          // this.loaderService.hide();
          return response;
      }));

}
}
