import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { LoaderState } from '../models/loader';

@Injectable()

export class LoaderService {

    private loaderSubject = new Subject<LoaderState>();
    private loaderSubjectNew = new Subject<LoaderState>();
    loaderState = this.loaderSubject.asObservable();

    loaderStateNew = this.loaderSubjectNew.asObservable();

    constructor() { }

    show() {
    //    console.log('hey')
        this.loaderSubject.next(<LoaderState>{ show: true });
    }

    hide() {
        this.loaderSubject.next(<LoaderState>{ show: false });
    }
    showNew() {

        this.loaderSubjectNew.next(<LoaderState>{ show: true });
        console.log('loader')
    }

    hideNew() {
        this.loaderSubjectNew.next(<LoaderState>{ show: false });
    }
}